.single_product_container {
	
	.single_product_thumbnails
	{
		height: 527px;
	}
	.single_product_thumbnails ul li
	{
		position: relative;
		height: 136px;
		margin-bottom: 30px;
		cursor: pointer;
	}
	.single_product_thumbnails ul li::after
	{
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border: solid 1px #fe4c50;
		content: '';
		visibility: hidden;
	}
	.single_product_thumbnails ul li.active::after,
	.single_product_thumbnails ul li:hover::after
	{
		visibility: visible;
	}
	.single_product_thumbnails ul li img
	{
		width: 100%;
	}
	.image_col
	{
		padding-right: 30px;
	}
	.single_product_image
	{
		width: 100%;
		height: 527px;
	}
	.single_product_image_background
	{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
	}


	.product_details
	{
		width: 100%;
	}
	.product_details_title
	{
		font-weight: 500px;
		line-height: 30px;
	}
	.product_details_title p
	{
		margin-top: 12px;
	}
	.free_delivery
	{
		background: #f5f5f5;
		margin-top: 53px;
	}
	.free_delivery i
	{
		display: inline-block;
	}
	.free_delivery span:last-child
	{
		display: inline-block;
		height: 40px;
		line-height: 40px;
		text-transform: uppercase;
	}
	.free_delivery span:first-child
	{
		display: inline-block;
		margin-right: 19px;
		font-size: 16px;
		-webkit-transform: translateY(-1px);
		-moz-transform: translateY(-1px);
		-ms-transform: translateY(-1px);
		-o-transform: translateY(-1px);
		transform: translateY(-1px);
	}
	.original_price
	{
		font-weight: 500;
		color: #b9b4c7;
		text-decoration: line-through;
		line-height: 30px;
		margin-top: 21px;
	}
	.product_price
	{
		display: inline-block;
		font-size: 24px;
		color: #fe4c50;
		font-weight: 500;
		line-height: 30px;
		margin-top: 2px;
	}
	.star_rating
	{
		display: inline-block;
		margin-left: 33px;
	}
	.star_rating li
	{
		display: inline-block;
		margin-right: -2px;
		cursor: pointer;
	}
	.star_rating li i
	{
		color: #fac451;
	}
	.product_color
	{
		margin-top: 27px;
	}
	.product_color ul
	{
		display: inline-block;
		margin-left: 26px;
	}
	.product_color ul li
	{
		display: inline-block;
		width: 12px;
		height: 12px;
		border-radius: 50%;
		margin-right: 29px;
		cursor: pointer;
	}
	.quantity
	{
		width: 100%;
		height: 40px;
		margin-top: 23px;
	}
	.quantity_selector
	{
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		width: 132px;
		height: 100%;
		border: solid 1px #e5e5e5;
		margin-left: 29px;
	}
	.quantity_selector span
	{
		font-weight: 500;
		color: #232530;
	}
	.plus,
	.minus
	{
		padding-left: 14px;
		padding-right: 14px;
		cursor: pointer;
	}
	.plus:hover,
	.minus:hover
	{
		color: #b5aec4;
	}
	.single_add_to_cart_button
	{
		width: 160px;
		margin-left: 19px;
		font-size: 12px !important;
	}
	.product_favorite
	{
		width: 40px;
		height: 100%;
		margin-left: 22px;
		border: solid 1px #e5e5e5;
		border-radius: 2px;
		cursor: pointer;
	}
	.product_favorite:hover::after
	{
		color: #fe4c50;
	}
	.product_favorite::after
	{
		font-family: 'FontAwesome';
		content:'\f08a';
		font-size: 14px;
	}
	.product_favorite.active::after
	{
		content:'\f004';
		color: #fe4c50;
	}
}